import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import "./Experience.css";
import { experience } from "../../portfolio.js";
import { Fade } from "react-reveal";

class Experience extends Component {
  render() {
    const theme = this.props.theme;

    // Directly get experiences from the "Work" section, bypassing any interaction requirement
    const workSection = experience.sections.find((section) => section.title === "Work");

    return (
      <div className="experience-main">
        <Header theme={theme} />
        <div className="basic-experience">
          <Fade bottom duration={2000} distance="40px">
            <div className="experience-heading-div">
              <div className="experience-heading-img-div">
                { 
                  <img
                  style={{
                    maxWidth: "100%",
                    height: "auto",
                  }}
                  alt="Career"
                  src={require(`../../assets/images/Reflection.jpeg`)}
                />
                }
              </div>
              <div className="experience-heading-text-div">
                <h1 className="experience-heading-text" style={{ color: theme.text }}>
                  {experience.title}
                </h1>
                <h3 className="experience-heading-sub-text" style={{ color: theme.text }}>
                  {experience.subtitle}
                </h3>
                <p className="experience-header-detail-text subTitle" style={{ color: theme.secondaryText }}>
                  {experience.description}
                </p>
              </div>
            </div>
          </Fade>
        </div>

        {/* Render the experiences list directly */}
        {workSection && (
          <div className="experience-list">
            {workSection.experiences.map((exp, index) => (
              <div key={index} className="experience-item">
                <h2>{exp.title}</h2>
                <h4>{exp.company}</h4>
                <p>{exp.duration}</p>
                <p>{exp.location}</p>
                <p>{exp.description}</p>
              </div>
            ))}
          </div>
        )}

        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

export default Experience;

